
@import url('https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Press+Start+2P&family=Roboto:wght@100;300;400&display=swap');

/* font-family: 'Bebas Neue', cursive;
font-family: 'Press Start 2P', cursive;
font-family: 'Roboto', sans-serif; */


/* color themes */
:root {
  --primary: #613dc1;
  --secondary: #4e148c;
  --tertiary: #2c0735;
  --quaternary: #858ae3;
  --left-offset: 40px;
 
}

body {
  margin: 0;
  background-color: var(--primary);
}


/* Nav bar */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 9px 10px;
  background-color: var(--secondary);
  box-shadow: 0px 2px 5px rgba(0, 0, 0, 0.1);
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
}

.logo-img {
  border-radius: 50%;
  width: 50px;
  height: 50px;
  border: 1px solid var(--tertiary);
}

.logo-text {
  font-family: 'Bebas Neue', cursive;
  margin: 0 1em;
  color: white;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: black;
  letter-spacing: 0.7em;
  font-size: 1.5rem;
  font-weight: 400;
}


.contact-button {
  text-decoration: none;
  font-family: 'Bebas Neue', cursive;
  color: var(--tertiary);
  background-color: var(--quaternary);
  padding: 0.7em 1em;
  margin: 0 2em;
  border: 2px solid var(--tertiary);
  border-radius: 10px;
  letter-spacing: 2px;

}

.contact-button:hover {
  color: white;
  background-color: var(--secondary);
  transform: scale(1.5);
}

@media screen and (max-width: 769px) {
  .logo-text {
    display: none;
  }
 
}



/* Introduction part */

.introduction .container {
  
  display: flex;
  flex-direction: row;
  /* flex-wrap: wrap; */
  align-items: center;
}

.introduction .box {
  /* width: 50%; */
  max-width: 50%;
  /* height: 200px; */
  /* box-sizing: border-box; */
  padding:  30px 50px;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  letter-spacing: 1.5px;
  color: white;
  font-weight: 400;
  font-size: 1.5em;
  -webkit-text-stroke-width: 0.5px;
  -webkit-text-stroke-color: black;
  
}

.introduction .box1 {
  text-align: justify;
}

.introduction img {
  width: 70%;
  border-radius: 10px;
}

.box h2 {
  color: var(--secondary);
  -webkit-text-stroke-width: 0.001em;
}

@media (max-width: 769px) {
  .introduction .container {
    display: block;
  }
  .introduction .box {
    max-width: 100%;
  }
}





/* Skills part */

.skills {
  background-color: var(--secondary);
  text-align: center;
  padding: 2em 0;
  font-family: 'Roboto', sans-serif;
  color: var(--quaternary);
  margin: 0;
}
.skills .container {

  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 2em 2em;
}


.skill img {
  width: 15em;

}

.skill span {
  display: none;
  position: absolute;
  top: 60rem;
  left: inherit !important;
  background-color: var(--quaternary);
  color: var(--tertiary);
  opacity: 0.7;
  padding: 5px;
  border-radius: 5px;
}

.skill img:hover {
  transform: scale(1.2);
  

}

.skill:hover span {
  display: block;
}


@media (max-width: 769px) {
  .skills .container {
    display: block;
  }
  .skill img {
    width: 30%;
  }
  .skill span {
    display: none;
  }
}



/* weather and youtube part  */


.apis .container {
  display: flex;
  flex-direction: row;
  align-items: space-around;
  padding: 2em 2em;
  background-color: var(--primary);
}

.weather {

  
  
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  width: 50%;

}

.weather h1 {
  font-family: 'Bebas Neue', cursive;
  color: var(--tertiary);
  font-size: 3em;
  margin: 0;
  padding: 0.5em 0;
}

.weather p {
  font-family: 'Bebas Neue', sans-serif;
  color: var(--tertiary);
  font-size: 1.5em;
  margin: 0;
  padding: 0.5em;
}


.material-icons {
  text-align: center;
  justify-content: center;
  align-items: center;
}

.apis .youtube {
  /* display: flex;
  flex-direction: column; */
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 50%;
}
.apis iframe {
  border-radius: 10px;
  width: 560px; 
  height: 315px;
}



@media (max-width: 769px) {
  .apis .container {
    display: block;

  }

  .apis .youtube {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
  }
  .apis .weather {
    width: 100%;
  }
}



/* connection links part */

.connections {
  background-color: var(--tertiary);
  text-align: center;
  padding: 1em 0;
  font-family: 'Roboto', sans-serif;
  color: var(--quaternary);
  margin: 0;
}

.connections .container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  padding: 1em 2em;
  background-color: var(--tertiary);
}

.connection img {
  width: 9em;

}

.connection span {
  display: none;
  position: absolute;
  top: 101rem;
  left: inherit !important;
  background-color: var(--quaternary);
  color: var(--tertiary);
  opacity: 0.7;
  padding: 5px;
  border-radius: 5px;
}

.connection img:hover {
  transform: scale(1.2);
  cursor: pointer;
  

}

.connection:hover span {
  display: block;
}

@media (max-width: 768px) {
  .connections .container {
    display: block;
  }

  /* .connection img {
    width: 30%;
  } */
  .connection span {
    display: none;
  }

  .connection {
    padding-bottom: 2em;
  } 

  .connection2 {
    padding-bottom: 3em;
  }
  .connection3 {
    padding-bottom: 5em;
  }
}


